import React, { Component } from 'react';
import axios from 'axios';
import { Route, NavLink } from 'react-router-dom';

import Reviews from './Reviews';

export default class MovieDetailsPage extends Component {
  state = {
    id: null,
    original_title: null,
    // overview: null,
    poster_path: null,
    release_date: null,
    runtime: null,
    title: null,
  };

  async componentDidMount() {
    const API_KEY = '0834b7bfd5fb91f9012c04990bc0fd7c';
    const { movieId } = this.props.match.params;

    try {
      const response = await axios.get(
        `https://api.themoviedb.org/3/movie/${movieId}?api_key=${API_KEY}&language=en-US`,
      );
      console.log(response.data);
      this.setState({ ...response.data });
    } catch (error) {
      console.log('error', error);
      return [];
    }
  }

  render() {
    const {
      title,
      poster_path,
      original_title,
      overview,
      runtime,
      release_date,
    } = this.state;
    const { match } = this.props;

    const Base_Url = 'https://image.tmdb.org/t/p/w200';

    return (
      <>
        <h2>Movie details: {title}</h2>
        <img src={`${Base_Url}${poster_path}`} alt={original_title} />
        <h3>{original_title}</h3>
        {/* <p>{overview}</p> */}
        {runtime && (
          <span>
            <b>Run time:</b> {runtime} min
          </span>
        )}
        <br />
        {release_date && (
          <span>
            <b>Release date:</b> {release_date}
          </span>
        )}
        <br />
        <NavLink to={`${match.url}/cast`}>Cast</NavLink>
        <br />
        <NavLink to={`${match.url}/reviews`}>Reviews</NavLink>
        {/* Пробовал роут сделать, для передачи пропсов */}
        {overview && (
          <Route
            path={`${match.path}/reviews`}
            render={props => {
              <Reviews {...props} overview={overview} />;
            }}
          />
        )}
      </>
    );
  }
}
